	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Just to see you again","date":"23 Oct 2000","slug":"/2000-10-23-just-to-see-you-again/","tags":"Y2000"},"html":"<p><b>Evening update:</b> Christopher ate a little for the first time, and he's been filling urine sample bags quite regularly. His blood oxygen his excellent, but blood gas is only so-so. He's starting to say doddle-doddle-doddle and daddle-daddle-daddle again, so he's returning to his usual self. </p>\n<hr />\nI finally went in to see Christopher at the Mariahilf clinic this morning. I only stayed for 10 minutes, since I'm at the endphase wheezing-and-coughing stage of my illness. Christopher was awake, but groggy, and he's wearing a cute hairnet for his IVs. The doctors are unhappy with his blood values, but he's slowly getting better. </p>\n<p>Mama is hanging in there too. She's been with him pretty much 24/7 since he was admitted on Saturday. </p>\n<hr />\nMaricopa County (Arizona) was kind enough spend $21 to send my absentee ballot by Express Mail. I'll be sending it back today, via normal air mail. I only voted for the federal races, skipping the local offices and the propositions (except for one banning non-language classes in public schools not taught in English... we couldn't let that get by without a NO).</p>"},"next":{"fields":{"title":"Other news","date":"22 Oct 2000","slug":"/2000-10-22-other-news/"}},"previous":{"fields":{"title":"This late word","date":"24 Oct 2000","slug":"/2000-10-24-this-late-word/"}}},{"node":{"fields":{"title":"Other news","date":"22 Oct 2000","slug":"/2000-10-22-other-news/","tags":"Y2000"},"html":"<p><b>Update:</b> Fever is down even further, but the two blood values the doctors are interested are still up, indicating a medium-sized lung infection. He's taking in fluids well, and starting to eat some. Christopher is in a 'single room with parent's bed', so Mama is with him all the time. I'll be taking shifts as soon as I am certified healthy.</p>\n<hr />\nThe world does not only revolve around Christopher...</p>\n<p>Strange but True Dept.: I was amused to see in my <a href=\"http://www.ix.de/ct\">c't</a> (<i>the</i> German computer magazine) yesterday an anti-Linux ad for Windows 2000 featuring <a href=\"http://www.koehntopp.de/kris/msad.jpg\">mutant penguins</a>. (Text: \"An open operating system doesn't only have advantages\".) According to <a href=\"http://slashdot.org\">slashdot</a>, it's the <a href=\"http://slashdot.org/articles/00/10/21/1644248.shtml\">first Microsoft ad that directly targets Linux</a>.</p>\n<p>You Can Choose Your Friends but You Can't Shake Your In-Laws Dept: Last weekend, when the Expo 2000 enjoyed record numbers of visitors near the 400,000 mark, Mama's two sisters-in-law accidently met on the Expo grounds.</p>"},"next":{"fields":{"title":"Not any better","date":"21 Oct 2000","slug":"/2000-10-21-not-any-better/"}},"previous":{"fields":{"title":"Just to see you again","date":"23 Oct 2000","slug":"/2000-10-23-just-to-see-you-again/"}}},{"node":{"fields":{"title":"Not any better","date":"21 Oct 2000","slug":"/2000-10-21-not-any-better/","tags":"Y2000"},"html":"<p><b>Update:</b> Christopher's fever is down to 38&deg;C (100.4&deg;F), he's stable, and already flirting with the nurses. :-)</p>\n<p>Things are only getting worse here. We took Christopher to the pediatrician on Thursday morning, and were told to keep giving him aspirin and to come back Monday if he didn't get any better. He didn't get better, and the aspirin didn't keep the fever down. We called the pediatrician today, who said not to panic and to wait another day. </p>\n<p>We didn't follow her advice. Mama took Christopher to the nearest children's clinic and they diagnosed pneumonia. He will be in the hospital for the next 10 days. </p>\n<p>As for me, I'm on antibiotics and bed rest for the next few days, so I can't even go visit him.</p>\n<p>Thanks, <a href=\"http://andrea.editthispage.com/\">Andrea</a>, for the get-well wishes. And yes, we did notice the <a href=\"http://andrea.editthispage.com/pictures/viewer$1334\">hippos</a> from the Expo :-)</p>"},"next":{"fields":{"title":"Return","date":"20 Oct 2000","slug":"/2000-10-20-return/"}},"previous":{"fields":{"title":"Other news","date":"22 Oct 2000","slug":"/2000-10-22-other-news/"}}}],"pathPrefix":"","first":false,"last":false,"index":1038,"pageCount":1101}}

